	  if (!this.prefix) {
	    switch (params[0]) {
	      case 5:
	        // status report
	        this.send('\x1b[0n');
	        break;
	      case 6:
	        // cursor position
	        this.send('\x1b['
	          + (this.y + 1)
	          + ';'
	          + (this.x + 1)
	          + 'R');
	        break;
	    }
	  } else if (this.prefix === '?') {
	    // modern xterm doesnt seem to
	    // respond to any of these except ?6, 6, and 5
	    switch (params[0]) {
	      case 6:
	        // cursor position
	        this.send('\x1b[?'
	          + (this.y + 1)
	          + ';'
	          + (this.x + 1)
	          + 'R');
	        break;
	      case 15:
	        // no printer
	        // this.send('\x1b[?11n');
	        break;
	      case 25:
	        // dont support user defined keys
	        // this.send('\x1b[?21n');
	        break;
	      case 26:
	        // north american keyboard
	        // this.send('\x1b[?27;1;0;0n');
	        break;
	      case 53:
	        // no dec locator/mouse
	        // this.send('\x1b[?50n');
	        break;
	    }
	  }
