	  if (y2 < y1) {
	    tmp = x2;
	    x2 = x1;
	    x1 = tmp;
	    tmp = y2;
	    y2 = y1;
	    y1 = tmp;
	  }
	
	  if (x2 < x1 && y1 === y2) {
	    tmp = x2;
	    x2 = x1;
	    x1 = tmp;
	  }
	
